
.wrapper {
  width: 100%;
  display: grid;
  grid-template-rows: 4rem 2rem 1fr 5rem;
  grid-template-columns: 20% 65% 15%;
  grid-template-areas: 
    'header header header'
    'navbar navbar navbar'
    'info content wijets'
    'footer footer footer';
  grid-gap: 0.5rem;
}

.header {
  grid-area: header;
}

.header img {
  width: 12rem;
}

.nav {
  background-color: aqua;
  grid-area: navbar;
  display: flex;
}

.content {
  background-color: darkblue;
  color: #fff;
  grid-area: content;
}

.content img {
  max-width: 100%;
}

.avatar img {
  width: 6rem;
}

.footer {
  text-align: center;
  background-color: cadetblue;
  color: #fff;
  grid-area: footer;
}